.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  height: 100%;
  min-height: 100vh;
  position: relative; /* Tambahkan properti position: relative; */
}

/* Pindahkan .App-content ke bawah .App-background */
.App-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("../assets/background.jpg");
  background-size: cover;
  background-position: center;
  filter: blur(5px);
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.App-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(45deg, rgba(76, 76, 76, 0.8), rgba(76, 76, 76, 0.8));
  padding: 20px;
  color: white;
  border-radius: 20px;
  width: 90%;
  max-width: 800px;
  box-sizing: border-box;
  z-index: 1;
  text-align: center;
}

/* Tambahkan properti position: relative; dan z-index: 2; untuk .button-container */
/* Perubahan pada tombol agar tidak bertumpuk pada perangkat seluler */
.button-container {
  position: relative;
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.button {
  background-color: #ffffff;
  color: #000000;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  border-radius: 5px;
  margin-right: 5px;
  margin-bottom: 10px; /* Tambahkan margin bawah untuk menjaga jarak antar tombol */
}

.button:hover {
  background-color: #00b7ff;
  color: #ffffff;
}

.button:active {
  background-color: #017fb1;
  color: #ffffff;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

/* Perubahan pada .card agar gambar tetap terlihat pada perangkat seluler */
.card {
  background-color: #ffffff;
  width: 100%; /* Lebar kartu 100% pada semua ukuran layar */
  max-width: 500px;
  margin-top: 10px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  text-align: center;
}

.card img {
  width: 100%;
  height: auto; /* Biarkan tinggi gambar menyesuaikan untuk tetap terlihat pada perangkat seluler */
  object-fit: cover;
  border-radius: 8px;
}

@media screen and (max-width: 768px) {
  .card {
    width: 90%; /* Lebar kartu 90% pada layar kecil */
    margin: 10px auto; /* Tengahkan kartu secara horizontal dan berikan jarak atas/bawah */
  }
}
